.local-video-wrapper {
  display: flex;
}
#localVideo {
  height: 112px;
  right: 0;
  margin: 24px auto;
  display: inline-block;
  border-radius: 4px;
}
.video-wrapper {
  height: 100vh;
  background: #252839;
  width: 100%;
}
#remoteVideo {
  background: transparent;
  margin: 0 auto;
  display: block;
  max-width: 100vw;
  height: 100%;
}
#remoteVideo.hide {
  display: none;
}
.video-wrapper .status {
  display: flex;
  justify-content: center;
  align-items: center;
  color: greenyellow;
  height: 100vh;
}

.react-grid-layout {
  position: relative;
  transition: height 200ms ease;
}
.react-grid-item {
  transition: all 200ms ease;
  transition-property: left, top;
}
.react-grid-item.cssTransforms {
  transition-property: transform;
}
.react-grid-item.resizing {
  z-index: 1;
  will-change: width, height;
}

.react-grid-item.react-draggable-dragging {
  transition: none;
  z-index: 3;
  will-change: transform;
}

.react-grid-item.react-grid-placeholder {
  background: red;
  opacity: 0.2;
  transition-duration: 100ms;
  z-index: 2;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.react-grid-item > .react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  bottom: 0;
  right: 0;
  background: url("data:image/svg+xml;base64,");
  background-position: bottom right;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  box-sizing: border-box;
  cursor: se-resize;
}

.w100{
  width: 100%;
  height: 100%;
}
.videoContainer{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.videoContainer video{
  width: 100%;
  height: 100%;
}
.controls{
  position: absolute;
  bottom: 24px;
  left: 24px;
}
.control-btn{
  position: relative;
  margin-right: 24px;
  background: transparent;
  outline: none;
  border: none;
  box-shadow: 1px 1px 8px black;
  border-radius: 50%;
  background-color: #252839;
  height: 64px;
  width: 64px;
  cursor: pointer;
}

body, html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
}
#root{
  height: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  background: #252839;
}
.enter-room-container{
  margin: auto;
  
}
.enter-room-container form{
  display: flex;
}
.enter-room-container input{
  height: 48px;
  background: transparent;
  color: #FFF;
  font-size: 20px;
  outline: none;
  border:none;
  border-bottom: 2px solid greenyellow;
}
.enter-room-container button {
  background: greenyellow;
  width: 100px;
  border: none;
  margin-left: 24px;
  font-size: 20px;
  color: #252839;
  text-transform: uppercase;
  font-weight: bold;
}

